/**UI**/
.footer {
  background-color: #5c5c5c;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.footer .contenedor-footer .icono {
  width: 3rem;
}
.footer .contenedor-footer .icono:hover {
  filter: drop-shadow(0px 0px 10px rgb(255, 255, 255));
}
.footer .contenedor-footer p {
  color: #FFFFFF;
}
.footer .contenedor-footer a {
  color: #FFFFFF;
}
.footer .contenedor-footer a:hover {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-underline-offset: 10px;
}
.footer .navegacion-footer {
  display: flex;
  flex-direction: column;
  visibility: visible;
  opacity: 1;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-height: 500px;
}
@media (min-width: 768px) {
  .footer .navegacion-footer {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.footer .navegacion-footer a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
  transition: all 1s ease;
}
.footer .navegacion-footer a:hover {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  text-underline-offset: 10px;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer img {
  width: 8rem;
  margin: 0 50px 20px 50px;
}

.grid-footer {
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(2, 1fr);
  margin: 1rem;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .grid-footer {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(4, 1fr);
    margin: 5rem;
    width: 80%;
    margin: 0 auto;
  }
}

