.iconos-nosotros {
  background-color: #5c5c5c;
  padding: 3rem;
  width: 80%;
}
.iconos-nosotros h4 {
  margin: 0;
  font-size: 1.5rem;
  color: #FFFFFF;
}
.iconos-nosotros span {
  font-size: 4rem;
  color: #FFFFFF;
  margin: 1rem;
  cursor: pointer;
  transition: transform 1s ease-in-out;
}
.iconos-nosotros span:hover {
  animation: jump 1s;
}
@keyframes jump {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-10px);
  }
}

